import React from "react";
import { useFonts, Poppins_800ExtraBold, Poppins_100Thin, Poppins_500Medium } from "@expo-google-fonts/poppins";
import AppLoading from "expo-app-loading";
import Login from "../components/Login";

function Loginstudent({ navigation }) {
  let [fontsLoaded] = useFonts({
    Poppins_800ExtraBold,
    Poppins_500Medium,
    Poppins_100Thin,
  });

  if (!fontsLoaded) {
    return <AppLoading />;
  }

  const handleLoginSuccess = () => {
    // Navigate to Calendar after successful login
    navigation.navigate("Calendar");
  };

  return <Login navigation={navigation} role="student" onLoginSuccess={handleLoginSuccess} />;
}

export default Loginstudent;
