import { useState } from "react";
import {
  SafeAreaView,
  View,
  Text,
  TouchableOpacity,
  Image,
  TextInput,
  KeyboardAvoidingView,
  ScrollView,
  Alert,
} from "react-native";
import ForgotPasswordModal from "./ForgotPasswordModel";
import { db } from "../../config/firebase";
import { getDoc, doc } from "firebase/firestore";
import * as SecureStore from "expo-secure-store";

import styles from "../styles/login";

const offlineUsers = {
  student: { password: "123", role: "student" },
  student2: { password: "pass2", role: "student" },
  student3: { password: "pass3", role: "student" },
  student4: { password: "pass4", role: "student" },
  student5: { password: "pass5", role: "student" },
  student6: { password: "pass6", role: "student" },
  student7: { password: "pass7", role: "student" },
  student8: { password: "pass8", role: "student" },
  student9: { password: "pass9", role: "student" },
  student10: { password: "pass10", role: "student" },
  student11: { password: "pass11", role: "student" },
  student12: { password: "pass12", role: "student" },
  student13: { password: "pass13", role: "student" },
  student14: { password: "pass14", role: "student" },
  student15: { password: "pass15", role: "student" },
  teacher: { password: "pass", role: "teacher" },
};

const Login = ({ navigation, role }) => {
  const [username, setUsername] = useState(null);
  const [password, setPassword] = useState(null);
  const [showModal, setShowModal] = useState(false);

  const save = async (key, value) => {
    await SecureStore.setItemAsync(key, value);
  };

  const handleLogin = async () => {
    if (!username || !password) {
      Alert.alert("Empty fields", "Please fill out all fields", [{ text: "OK" }]);
      return;
    }

    if (offlineUsers[username] && offlineUsers[username].password === password) {
      if (offlineUsers[username].role !== role) {
        Alert.alert("Access Denied", "You are not authorized for this role", [{ text: "OK" }]);
        return;
      }

      save("user", JSON.stringify({ username, ...offlineUsers[username] }));
      navigation.reset({
        index: 0,
        routes: [{ name: role === "student" ? "Calendar" : "Teacherhome" }],
      });
      return;
    }

    const usersRef = doc(db, "users", username);

    try {
      const userSnap = await getDoc(usersRef);

      if (!userSnap.exists()) {
        Alert.alert("Invalid credentials", "Invalid username or password", [{ text: "OK" }]);
        return;
      }

      const userData = userSnap.data();

      if (userData.role !== role) {
        Alert.alert("Access Denied", "You are not authorized for this role", [{ text: "OK" }]);
        return;
      }

      if (userData.password === password) {
        const id = userSnap.id;
        save("user", JSON.stringify({ ...userData, id }));
        navigation.reset({
          index: 0,
          routes: [{ name: role === "student" ? "Calendar" : "Teacherhome" }],
        });
      } else {
        Alert.alert("Invalid credentials", "Invalid username or password", [{ text: "OK" }]);
      }
    } catch (error) {
      console.log("Firebase Error:", error);
      Alert.alert("Error", "Failed to connect. Please check your internet connection.", [{ text: "OK" }]);
    }
  };

  return (
    <KeyboardAvoidingView behavior="height" style={{ flex: 1 }}>
      <ScrollView style={styles.container}>
        <SafeAreaView>
          <View style={styles.Box}>
            {role === "student" && (
              <Image style={styles.img} source={require("../../assets/student.png")} />
            )}
            {role === "teacher" && (
              <Image
                style={styles.img}
                resizeMode="contain"
                source={require("../../assets/teacher.png")}
              />
            )}
          </View>
          <Text style={styles.head}>Login as a {role}</Text>
          <View style={styles.inpbox}>
            <TextInput
              style={styles.input}
              placeholder="Username"
              onChangeText={setUsername}
              value={username}
              autoCapitalize="none"
            />
            <TextInput
              style={styles.input}
              onChangeText={setPassword}
              value={password}
              secureTextEntry
              placeholder="Password"
            />
            <TouchableOpacity style={styles.button} onPress={handleLogin}>
              <Text style={styles.btn}>Sign in</Text>
            </TouchableOpacity>
            <TouchableOpacity onPress={() => setShowModal(true)}>
              <Text style={styles.forgot}>Forgot password?</Text>
            </TouchableOpacity>
          </View>
          <ForgotPasswordModal visible={showModal} setShowModal={setShowModal} />
        </SafeAreaView>
      </ScrollView>
    </KeyboardAvoidingView>
  );
};

export default Login;
